import api from '../utils/api'
import Http from '../utils/Http'

// 获得回复 ( get )
export const replay_message = async (text)=>{
    let result = await Http.fetch_get(api.Reply_API+text);
    if(result.error_code === 0){
      //发送成功
      return result.result;
    }else {
      //发送失败
      console.log("发送失败了....");
      return null;
    }
  }