import React, { Component } from 'react'
import Header from "../../textbook/components/header"
import MyIscorll from "../../common/myiscroll/myiscroll"
import {replay_message} from "../../../services/mentoringService"
import 'whatwg-fetch'
import "./style.scss"
 

const InputBottom=(props)=>(
    <div className="inputBox">
        <div className="text">
            <span className="iconfont icon-yuyin"></span>
            <textarea onKeyUp={props.textChage} />
            <p className="textshow overlay">
                {props.text}
            </p>
            <span className="iconfont icon-Ovalx"></span>
        </div>
    </div>
)

export default class mentoring extends Component {
    constructor(props){
        super(props);
        this.state={
            text: "",
            chartData:[
                {
                    attr:"http://img3.imgtn.bdimg.com/it/u=3982785370,395932617&fm=26&gp=0.jpg",
                    text:"疑问在这里发出来哦....",
                    name:"在线客服"
                }
            ]
        }
    }

   textChage(ev){
       if(ev.keyCode===13){
           let val = ev.target.value;
           ev.target.value="";
            replay_message(val)
            .then(rs=>{
                this.setState({
                    text:"",
                    chartData:[
                        ...this.state.chartData,
                        {
                            attr:"http://img1.imgtn.bdimg.com/it/u=2588807168,3744006405&fm=15&gp=0.jpg",
                            text:val,
                            name:"我"
                        },
                        {
                            attr:"http://img3.imgtn.bdimg.com/it/u=3982785370,395932617&fm=26&gp=0.jpg",
                            text:rs.text,
                            name:"在线客服"
                        }
                    ]
                });
                window.myScorll.scrollTo(0, window.myScorll.maxScrollY, 0);
            })
        }
    }

    back(){
        this.props.history.goBack();
    }

    render() {
        let {text,chartData} = this.state;
        return (
            <div className="page border-top" id="mentoring">
                <Header
                centent="答疑"
                right="投诉"
                back={this.back.bind(this)}
                rightAction={this.rightAction}/>

                <MyIscorll top="44px" bottom="49px">
                    <ul className="messageBox"
                    style={{paddingBottom:"10px"}}>

                    <>
                     <div className="doubt">
                         <p>· 习题不会做？</p>
                         <p>· 老师讲的知识点没听懂？</p>
                         <p>· 想给点意见？</p>
                         <p>· 其他疑问？</p>
                         <img 
                         src="/img/doubt.jpg" 
                         alt="tup"/>
                     </div>
                     {chartData.map((item,index)=> 
                       <li key={index}>
                           <img src={item.attr} alt="图片"/>
                           <span className="nickname">{item.name}</span>
                           <span className="text">
                              {item.text}
                           </span>
                       </li>
                    )}
                    </>
                    </ul>
                </MyIscorll>

                <InputBottom
                text={text}
                textChage={this.textChage.bind(this)}/>
            </div>
        )
    }

    rightAction(){
        window.mui.toast("系统升级中，请稍后重试...");
    }

}
